//=============== gizmo masked-text-input classes ================//

.giz-masked-text-input {
    input {
        font-family: $font-family-text;
        @include font-l($font-weight-light);
    }

    .giz-mask {
        color: rgba(255, 255, 255, 0.35);
        display: flex;
        //FontF
        font-family: $font-family-text;
        @include font-l($font-weight-light);

        &-done {
            visibility: hidden;
        }
    }

    .giz-input-root {
        &--small {
            .giz-mask {
                @include font-m($font-weight-light);
            }
        }

        &--medium {
            .giz-mask {
                @include font-l($font-weight-light);
            }
        }

        &--large {
            .giz-mask {
                @include font-l($font-weight-light);
            }
        }
    }
}